.sticky-book-consultation-wrap{
    position: fixed;
    // right: -8px;
    right: -175px;
    bottom: 50px;
    width: 280px;
    z-index: 9;
    transition: all 0.6s;
    &.close-sticky{
        right: -8px;
        .click-link{
            height: 30px;
            width: 30px;
            left: 100px;
            span{
                opacity: 1;
            }
        }
    }
    .click-link{
        height: 100%;
        width: 120px;
        position: absolute;
        border-radius: 50% 50% 50% 0;
        span{
            background-color: $color__deep_red;
            border: 4px solid $color__white;
            height: 30px;
            width: 30px;
            border-radius: 50%;
            position: absolute;
            left: 0;
            opacity: 0;
            transition: all 0s;
            &:before{
                content: '';
                background-color: $color__white;
                height: 15px;
                width: 2px;
                transform: rotate(45deg);
                position: absolute;
                left: 10px;
                top: 4px;
            }
            &:after{
                content: '';
                background-color: $color__white;
                height: 15px;
                width: 2px;
                transform: rotate(-45deg);
                position: absolute;
                left: 10px;
                top: 4px;
            }
        }
    }
    a{
        display: block;
    }
}
.site-footer{
    .row{
        padding: 40px 0;
        border-top: 1px solid $color__brownish_grey;
        border-bottom: 1px solid $color__brownish_grey;
    }
    .footer-menu{
        @include breakpoint(small){
            margin-top: 20px;
        }
        ul{
            margin: 0;
            padding: 0;
            li{
                list-style: none;
                a{
                    font-size: 12px;
                    font-weight: bold;
                    line-height: 1.5;
                    letter-spacing: normal;
                    text-align: left;
                    color: $color__black;
                    text-decoration: none;
                    &:hover{
                        color: $color__rusty_red;
                    }
                }
            }
        }
    }
    .social-wrap{
        @include breakpoint(small){
            margin-top: 20px;
        }
        ul{
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            @include breakpoint(small){
                align-items: flex-start;
                flex-direction: row;
            }
            li{
                list-style: none;
                margin-bottom: 1px;

                @include breakpoint(small){
                    margin-right: 1px;
                }
                a{
                    background-color: $color__black_1;
                    width: 30px;
                    height: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    &:hover{
                        background-color: $color__rusty_red;
                    }
                }
            }
        }
    }
}